@font-face {
    font-family: "Roboto";
    font-weight: 400;
    src: url('./assets/Roboto/Roboto-Regular.ttf');
    font-display: swap;
}
:root {
    --first-color: white;
    --secondary-color: #FAFAFA;
    --text-color: black;
    --secondary-text-color: rgb(110, 97, 81);
    --shadow: 0px 1px 2px rgba(170, 170, 170, .4);
    --font-family: 'Inter';
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: var(--font-family);
    color: var(--text-color);
    background-color: white;
}

hr {
  margin: 2rem 0;
  border: 0;
  border-top: 1px solid #CCC;
}
.applitoolslogo {
    height: 81px;
    width: 81px;

}
main {
    height: 100vh;
    display: grid;
    grid-template-columns: 300px auto;
    text-align: center;
}

h1, h2, h3 {
    /*padding: 1rem;*/
    margin: 1.5rem 0 0 0;
    font-size: larger;
    /*font-weight: bold;*/
}

#form {
  background-color: white;
  padding: 2rem;
  border-radius: 3px;
  box-shadow: var(--shadow);
  max-width: 100px;
}

/* Animation */
@keyframes fadein {
  from { opacity: 0; transform: translateY(-10px); }
  to   { opacity: 1; transform: translateY(0); }
}
/* /Animation */

#form h1 {
  font-size: 1rem;
    /*margin: 1rem 0 0 0;*/
  margin-top: 0;
  margin-bottom: .8rem;
  /*text-transform: capitalize;*/
}

#logo {

    /*color: var(--first-color);*/
    /*margin-bottom: .5rem;*/
}

#side-menu-solid {
    background-color: #1a847b;

}
#side-menu {
    font-size: 1rem;
    padding: 1.2rem;
    background-color: var(--secondary-color);
    justify-content: center;
    align-items: center;
    text-align: center;
}
#login-content {
    height: 75%;
    max-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: larger;
    background-color: white;
}

#side-menu ul {
    list-style: none;
}

#side-menu ul li {
     /*border-bottom: 1px solid var(--secondary-text-color); */
    padding: .8rem 0;

}

#side-menu a {
    color: black;
    text-decoration: none;
    font-size: large;
    display: block;
}

#side-menu a:hover {
  color: black;
}

#side-menu a.active {
  color: black;
}

#main-content {
    padding: 2rem;
    overflow: auto;
    /*animation: fadein .3s;*/
    position: relative;
}

#main-content .account {
    padding: 2rem;
    border-radius: 3px;
    background-color: white;
    box-shadow: var(--shadow);
}

#main-content h3 {
  font-weight: normal;
  text-transform: uppercase;
}

#main-content .balance {
    font-weight: 800;
    font-size: 2rem;
}

#main-content .details {
    display: flex;
    justify-content: center;
    flex-direction: column;
    /*display: grid;*/
    /*grid-template-columns: 200px auto 100px;*/
    /*padding: .8rem;*/
}

#actions {
    margin-top: 1rem;
}

#actions button {
    background-color: var(--first-color);
    color: var(--secondary-text-color);
    padding: .5rem;
    font-size: 1.1rem;
    border: 0;
    cursor: pointer;
    transition: .3s;
    border-radius: 3px;
    margin-right: .3rem;
}

#actions button:hover {
    background-color: rgba(219, 204, 163, .3);
    color: var(--text-color);
}

#transactions {
    /*padding: 2rem;*/
    /*border-radius: 3px;*/
    /*box-shadow: var(--shadow);*/
    text-align: left;
    
}

#transaction-div {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}

.transaction-item {
  display: grid;
  grid-template-columns: 200px auto 100px;
  padding: .8rem;
    text-align: left;

}

#transactions table {
    border-collapse: collapse;
    table-layout: auto;
    width: 100%;
}

#transactions th {
    padding-bottom: 1rem;
    font-weight: normal;
}

#transactions table th, td  {
    border-bottom: 1px solid rgba(59, 49, 40, .2);
}

#transactions table tr {
}

#transactions table td:nth-child(1), #transactions table td:nth-child(2), 
    #transactions th td:nth-child(2) {
    text-align: left;
    padding: .8rem;
}

#transactions table td:nth-child(3), #transactions table td:nth-child(4) {
    text-align: right;
    padding: .8rem;
}

#login-page {
}

label {
  width: 100%;
  display: block;
}
#login {
}

#login #logo {
}

#login label, label {
  text-transform: uppercase;
  /*margin-bottom: .3rem;*/
  /*margin-top: .6rem;*/
  /*  padding-left: .25rem;*/
  color: black;
}

#login input, input, select, textarea {
  font-family: var(--font-family);
    color: var(--text-color);
  /*outline: none;*/

  font-size: 1.25rem;
    margin: 1rem 0 0 0;
  padding: .75rem .5rem;
  border-radius: 3px;
  border: 1px solid var(--text-color);
    width: 401px;
    height: 59px;
}

.btn, .btn2 {
    /*margin: auto;*/
  background-color: #1A847B;
  color: white;
  padding: .7rem;
  font-size: larger;
    font-weight: bold;
  border: 0;
  cursor: pointer;
  transition: .3s;
  border-radius: 3px;
  margin: 1rem 0 0 0;
  /*margin-bottom: 2rem;*/
  display: block;
    width: 401px;
}

.btn2 {
  display: inline-block;
  width: auto;
}

.btn:hover, .btn2:hover {
  /*background-color: var(--text-color);*/
  color: white;
}

.notif {
  display: block;
  width: 401px;
  text-align: center;
  padding: .5rem;
  border-radius: 3px;
}

.danger {
  color: red;
  background-color: rgba(255, 0, 0, .1);
}

.success {
  color: #1A847B;
  background-color: rgba(0, 128, 0, .1);
}

input:disabled {
  border: 1px solid #CCC;
}

.right {
  text-align: right;
}

.left {
  text-align: left;
  padding-left: 0;
}

.transfer-icon {
  text-align: center;
  font-size: 3rem;
  margin-top: 1rem;
}

.big-input {
  font-size: 2rem;
  font-weight: 800;
}

.main {
  font-size: 2rem;
  margin-bottom: 1rem;
}

#form.budget {
  width: 100%;
  max-width: 1280px;
}

.budget-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.overlay {
  width: 100%;
  height: 100vh;
  position: absolute;
  top:0;
  left: 0;
  background-color: rgba(126, 126, 126, 0.5);
}
.modal {
  animation: fadein .3s;
  background-color: white;
  padding: 2rem;
  border-radius: .5rem;
  box-shadow: var(--shadow);
  width: 500px;
  position: absolute;
  left: calc(50% - 250px);
  top: calc(40% - 250px);
  text-align: center;
}

.modal button {
  margin: .3rem;
  margin-top: 1.3rem;
}

.modal label {
  text-align: left;
}

.btn-muted {
  background-color: rgba(219, 204, 163, .3);
  color: var(--secondary-text-color);
}

.modal input[type=number] {
  text-align: right;
}

.modal textarea {
  height: 100px;
}

.modal h2 {
  margin-bottom: 1rem;
}

.budget-row {
  padding: .8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.odd {
}

.even {
  background-color: rgba(238, 238, 238, .5);
}

.budget-title {
  display: inline-block;
  width: 100%;
}

.budget-title span {
  cursor: pointer;
}

.mr {
  margin-right: .7rem;
  font-size: 1.3rem;
  background-color: rgba(219, 204, 163, .4);
  color: var(--text-color);
  border: none;
  padding: .1rem .3rem;
  padding-top: .4rem;
  border-radius: 5px;
  font-weight: 800;
}

.mr:hover {
  background-color: rgba(255, 0, 0, .2);
  color: red;
  cursor: pointer;
}

.title {
  text-transform: capitalize;
}
